<template>
  <div class="header-block">
    <parallax :fixed="false" class="position-absolute">
      <img src="../assets/images/mpc1.jpg" alt="gdgdg">
    </parallax>
    <div class="container-fluid height_full dark_background" slot="loading">
      <div class="row align-items-center height_full">
        <div class="col">
          <h1>{{ header }}</h1>
          <p>{{ desc }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Parallax from 'vue-parallaxy'
export default {
  components: {
    Parallax
  },
  name: 'Aboute',
  data () {
    return {
      header: 'Я - Юрий Щербаченко',
      desc: 'html-верстальщик, фрилансер'
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.position-absolute img {
  height: auto;
  width: 100%;
}
.header-block {
  height: 100vh
}
.position-absolute {
  height: 100vh !important;
  width: 100%
}
.dark_background {
  background: rgba(0,0,0, .4);
}
h1, h2 {
  font-size: 37pt;
  font-weight: normal;
  color: #fff
}
p {
  color: #faeada;
  font-size: 27.5pt
}
@media screen and (max-width: 1000px) {
  .position-absolute img {
    width: auto;
  }
}
</style>



